
@media(max-width:1300px){

    // for top starts

    .top{
        padding: 16px 20px;
    }

    // top ends

    // nav starts

    nav{
        padding: 0 20px;
    }

    // nav ends

    // services start


    // services ends
    .services, .courses{
        .cards{
            > div{
                max-width: 48%;
            }
        }
    }
}

@media(max-width: 850px){

    // top starts
    .top{
        flex-wrap: wrap;

        &-left{
            flex-wrap: wrap;

   
        }

       
    }

    // top ends

    // for nav starts

    nav{
        ul{
            display: none;
        }

        .down-lists{
            display: block;
            padding: 0 15px;
        }
    }

    .mobile-nav{

        .center{
            flex-direction: column;
        }
        ul{
            display: block;
         
            li:not(:last-child){
                border-bottom: 1px solid $border-color;
            }

        }

      
    }

    // nav ends

    // banner starts

    .banner{
        h1{
            font-size: 38px;
            max-width: 100%;
        }
    }

    // banner ends

    // services start
    .services, .courses{
        .cards{
            flex-direction: column;
            > div{
                max-width: 95%;
            }
        }
    }

    // services ends

    // courses start
    .hide-sell{
        display: none;
    }

    // courses ends

 
}

@media(max-width:620px){

    // top starts

    .top{
        // font-size: 14px;
        &-left{
            &__email{
                margin-left: 0;
            }
        }

       

    }

    // top ends


    // banner starts


    .banner{
        h1{
            text-align: center;
            font-size: 30px;
        }
    }

    // banner ends
}

@media(max-width:360px){

    // top starts

    .top{
        &-right{
            flex-wrap:wrap; 
        }
    }

    // top ends

    // courses start
    .courses{
        .cards{
            > div{
                .for-service-description{
                    h4{
                        font-size: 20px;
                        width: 200px;   
                    }
                }
            }
        }
    }
    // courses ends
}